export default {
    name: 'UniSInput',
    props: {
        placeholder: { // 输入框空时占位符
            type: String,
            default: '请输入'
        },
        placeholderClass: { // placeholder的class，方便自定义placeholder样式
            type: String,
            default: 'input-placeholder'
        },
        id: { // 唯一标识符，当多个输入框时获取对应输入框的值
            type: String,
            default: 'inputValue'
        },
        type: { // 输入框类型
            type: String,
            default: 'text'
        },
        label: {
            type: String,
            default: '名称'
        },
        initValue: {
            type: String,
            default: ''
        },
		pvalue:{
			type: String,
			default: ''
		}
    },
    data () {
        return {
            value:this.pvalue
        }
    },
    methods: {
        restInput () {
            this.value = ''
            this.$emit('inputValue',this.value)
        },
        inputChange (e) {
            this.value = e.detail.value
            this.$emit('inputValue',e.detail.value)
        },
				setValue(value){
					this.value=value;
				}
    },
    created () {
        this.value = this.initValue
        this.$emit('inputValue',this.initValue)
    }
}
